.clearfix:after,.clearfix:before{content:"";display:table}.clearfix:after{clear:both}.read-more{color:#bca51d;text-decoration:underline;font-family:Arial,sans-serif;font-size:1.17em}.paginate a,.paginate ul li a{text-decoration:none;color:#fff}.btn{padding:12px 30px;border-radius:0;background-color:#bca51d;color:#fff}.paginate{background-color:#427ca2}.paginate ul li.active,.paginate ul li:hover{background-color:#bca51d}.paginate .col-md-10{float:none;margin:0 auto;text-align:center;overflow:hidden}.paginate .list,.paginate .next,.paginate .prev{display:inline-block;font-size:2em;font-weight:300;padding:15px 0}.paginate .list a,.paginate .next a,.paginate .prev a{padding:0 .5%;margin:0 10px}.paginate ul li{list-style:none;display:inline-block;padding:8px 13px}.paginate ul li:hover{cursor:pointer}@media screen and (max-width:992px){.paginate .next,.paginate .prev{display:none}}